const TableComp: React.FC<TableCompType> = ({ searchValue }) => {
  const [data, setData] = useState<dataUserType[]>()
  const [tableParams, setTableParams] = useState<TableParams>({
    pagination: {
      current: 1,
      pageSize: 10,
    },
  })

  const setValues = () => {
    setData(dataUser)
    setTableParams({
      ...tableParams,
      pagination: {
        ...tableParams.pagination,
      },
    })
  }

  useEffect( () => {
    setValues()
  }, [])

  useEffect( () => {
    setValues()
  }, [tableParams.pagination?.pageSize])

  useEffect( () => {
    if (searchValue.trim().length) {
      const searchStr = searchValue.toLowerCase().trim()
      const newData = data?.filter( item => item.fullName.toLowerCase().includes(searchStr) === true)
      setData(newData)
    } else {
      setValues()
    }
  }, [searchValue])

  const handleTableChange = (
    pagination: TablePaginationConfig,
    filters: Record<string, FilterValue | null>,
    sorter: SorterResult<dataUserType> | SorterResult<dataUserType>[],
  ) => {
    setTableParams({
      pagination,
      filters,
      ...sorter,
    })

    if (pagination.pageSize !== tableParams.pagination?.pageSize) {
      setData([]);
    }
  }

  return (
    <div className={styles.tableWrapper}>
       <Table
          columns={columns}
          rowKey={(data) => data.id}
          dataSource={data}
          pagination={{
            position: ['bottomCenter'],
            showSizeChanger: true,
            pageSizeOptions: ['5', '10', '15'],
            defaultPageSize: 10,
            defaultCurrent: 1,
            showTotal:  (total, range) => (
              <span style={{ left: 40, position: "absolute" }}>
                {` ${range[0]} - ${range[1]}  ${total} `}
              </span>
            ),
            ...tableParams.pagination
          }}
        onChange={handleTableChange}
        size="small"
        scroll={{
          x: 3520,
          y: 400
        }}
        bordered={true}
        rowClassName={(record, index) => index % 2 === 0 ? styles['table-row-light'] :  styles['table-row-green']}
      />
    </div>
  )
}

export default TableComp